'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';

interface QualifiedCandidate {
    candidateId: string;
    fileId: string;
    name: string;
    email: string;
    phone: string;
    experience: number;
    skills: string[];
    licenses: string[];
    location: string;
    qualifyingMatches: Array<{
        jobId: string;
        jobTitle: string;
        department: string;
        location: string;
        score: number;
        threshold: number;
        reasons: string[];
        matchedSkills: string[];
    }>;
    highestScore: number;
    totalQualifyingJobs: number;
}

interface Stats {
    totalCVs: number;
    totalJobs: number;
    qualifiedCandidates: number;
    matchRate: number;
}

export default function ResultsPage() {
    const [candidates, setCandidates] = useState<QualifiedCandidate[]>([]);
    const [stats, setStats] = useState<Stats | null>(null);
    const [loading, setLoading] = useState(true);
    const [sortBy, setSortBy] = useState('score');
    const [filterMinScore, setFilterMinScore] = useState<number | null>(null);

    useEffect(() => {
        fetchResults();
    }, [sortBy, filterMinScore]);

    const fetchResults = async () => {
        try {
            setLoading(true);
            const params = new URLSearchParams();
            if (sortBy) params.append('sortBy', sortBy);
            if (filterMinScore) params.append('minScore', filterMinScore.toString());

            const response = await fetch(`/api/results?${params.toString()}`);
            const data = await response.json();

            if (data.success) {
                setCandidates(data.qualifiedCandidates);
                setStats(data.stats);
            }
        } catch (error) {
            console.error('Error fetching results:', error);
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="min-h-screen bg-gray-50">
            {/* Hero Section */}
            <div className="bg-white border-b">
                <div className="container mx-auto px-6 py-12 max-w-7xl">
                    <div className="flex justify-between items-center">
                        <div>
                            <h1 className="text-4xl font-bold text-evernurse-dark mb-4">
                                Qualified Candidates
                            </h1>
                            <p className="text-lg text-gray-600 max-w-2xl">
                                Review candidates who meet your job requirements.
                                Filtered by AI matching scores and specific criteria.
                            </p>
                        </div>
                        <Link
                            href="/upload"
                            className="hidden md:flex bg-evernurse-teal text-white px-6 py-3 rounded-lg font-medium hover:bg-[#008f85] transition-all shadow-md hover:shadow-lg items-center gap-2"
                        >
                            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
                            </svg>
                            Upload More CVs
                        </Link>
                    </div>

                    {/* Stats Cards */}
                    {stats && (
                        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mt-12">
                            <StatCard
                                title="Total CVs Processed"
                                value={stats.totalCVs}
                                icon="ðŸ“„"
                                color="text-blue-600"
                                bg="bg-blue-50"
                            />
                            <StatCard
                                title="Active Job Openings"
                                value={stats.totalJobs}
                                icon="ðŸ’¼"
                                color="text-purple-600"
                                bg="bg-purple-50"
                            />
                            <StatCard
                                title="Qualified Candidates"
                                value={stats.qualifiedCandidates}
                                icon="âœ…"
                                color="text-evernurse-teal"
                                bg="bg-teal-50"
                            />
                            <StatCard
                                title="Overall Match Rate"
                                value={`${stats.matchRate}%`}
                                icon="ðŸ“Š"
                                color="text-orange-600"
                                bg="bg-orange-50"
                            />
                        </div>
                    )}
                </div>
            </div>

            <div className="container mx-auto px-6 py-8 max-w-7xl">
                {/* Filters & Sorting */}
                <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6 mb-8">
                    <div className="flex flex-wrap gap-6 items-center">
                        <div className="flex items-center gap-3">
                            <label className="text-sm font-medium text-gray-700">Sort by:</label>
                            <select
                                value={sortBy}
                                onChange={(e) => setSortBy(e.target.value)}
                                className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-evernurse-teal focus:border-transparent bg-white text-gray-700"
                            >
                                <option value="score">Highest Score</option>
                                <option value="experience">Most Experience</option>
                                <option value="name">Name (A-Z)</option>
                            </select>
                        </div>

                        <div className="flex items-center gap-3">
                            <label className="text-sm font-medium text-gray-700">Min Score:</label>
                            <input
                                type="number"
                                min="0"
                                max="100"
                                value={filterMinScore || ''}
                                onChange={(e) => setFilterMinScore(e.target.value ? parseInt(e.target.value) : null)}
                                placeholder="Any"
                                className="w-24 px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-evernurse-teal focus:border-transparent"
                            />
                        </div>

                        <button
                            onClick={() => { setSortBy('score'); setFilterMinScore(null); }}
                            className="ml-auto text-sm text-evernurse-teal hover:text-[#008f85] font-medium flex items-center gap-1"
                        >
                            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                            </svg>
                            Reset Filters
                        </button>
                    </div>
                </div>

                {/* Loading State */}
                {loading && (
                    <div className="flex items-center justify-center py-20">
                        <div className="text-center">
                            <svg className="animate-spin h-12 w-12 mx-auto mb-4 text-evernurse-teal" fill="none" viewBox="0 0 24 24">
                                <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                                <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                            </svg>
                            <p className="text-gray-600 font-medium">Loading results...</p>
                        </div>
                    </div>
                )}

                {/* Empty State */}
                {!loading && candidates.length === 0 && (
                    <div className="text-center py-20 bg-white rounded-xl border border-gray-200 shadow-sm">
                        <div className="inline-block p-6 bg-gray-50 rounded-full mb-6">
                            <svg className="w-16 h-16 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                            </svg>
                        </div>
                        <h3 className="text-2xl font-bold text-evernurse-dark mb-3">No Qualified Candidates Yet</h3>
                        <p className="text-gray-600 mb-8 max-w-md mx-auto">
                            Upload CVs and create job descriptions to see matching results here.
                        </p>
                        <div className="flex gap-4 justify-center">
                            <Link
                                href="/upload"
                                className="bg-evernurse-teal text-white px-6 py-3 rounded-lg font-medium hover:bg-[#008f85] transition-colors shadow-md"
                            >
                                Upload CVs
                            </Link>
                            <Link
                                href="/jobs"
                                className="bg-white text-evernurse-dark border border-gray-300 px-6 py-3 rounded-lg font-medium hover:bg-gray-50 transition-colors"
                            >
                                Manage Jobs
                            </Link>
                        </div>
                    </div>
                )}

                {/* Candidate Cards */}
                {!loading && candidates.length > 0 && (
                    <div className="space-y-6">
                        {candidates.map((candidate) => (
                            <CandidateCard key={candidate.candidateId} candidate={candidate} />
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
}

// Stats Card Component
function StatCard({ title, value, icon, color, bg }: {
    title: string;
    value: string | number;
    icon: string;
    color: string;
    bg: string;
}) {
    return (
        <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6 flex items-center gap-4 hover:shadow-md transition-shadow">
            <div className={`w-12 h-12 ${bg} rounded-lg flex items-center justify-center text-2xl`}>
                {icon}
            </div>
            <div>
                <p className="text-sm text-gray-500 font-medium">{title}</p>
                <p className={`text-2xl font-bold ${color}`}>{value}</p>
            </div>
        </div>
    );
}

// Candidate Card Component
function CandidateCard({ candidate }: { candidate: QualifiedCandidate }) {
    const [expanded, setExpanded] = useState(false);

    return (
        <div className="bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden hover:shadow-md transition-all duration-300">
            {/* Header */}
            <div className="p-6 border-b border-gray-100">
                <div className="flex justify-between items-start">
                    <div className="flex-1">
                        <div className="flex items-center gap-3 mb-2">
                            <h3 className="text-2xl font-bold text-evernurse-dark">{candidate.name}</h3>
                            <span className="bg-teal-50 text-evernurse-teal text-xs px-2 py-1 rounded-full font-bold border border-teal-100">
                                {candidate.highestScore}% Match
                            </span>
                        </div>

                        <div className="flex flex-wrap gap-6 text-sm text-gray-600 mb-4">
                            {candidate.email && (
                                <div className="flex items-center gap-2">
                                    <svg className="w-4 h-4 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                                    </svg>
                                    {candidate.email}
                                </div>
                            )}
                            {candidate.phone && (
                                <div className="flex items-center gap-2">
                                    <svg className="w-4 h-4 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 5a2 2 0 012-2h3.28a1 1 0 01.948.684l1.498 4.493a1 1 0 01-.502 1.21l-2.257 1.13a11.042 11.042 0 005.516 5.516l1.13-2.257a1 1 0 011.21-.502l4.493 1.498a1 1 0 01.684.949V19a2 2 0 01-2 2h-1C9.716 21 3 14.284 3 6V5z" />
                                    </svg>
                                    {candidate.phone}
                                </div>
                            )}
                            <div className="flex items-center gap-2">
                                <svg className="w-4 h-4 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 13.255A23.931 23.931 0 0112 15c-3.183 0-6.22-.62-9-1.745M16 6V4a2 2 0 00-2-2h-4a2 2 0 00-2 2v2m4 6h.01M5 20h14a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                                </svg>
                                <span className="font-medium text-gray-900">{candidate.experience} years</span> experience
                            </div>
                        </div>

                        {/* Skills & Licenses */}
                        <div className="flex flex-wrap gap-2">
                            {candidate.skills.slice(0, 5).map((skill, idx) => (
                                <span key={idx} className="bg-gray-100 text-gray-700 text-xs px-3 py-1 rounded-full border border-gray-200">
                                    {skill}
                                </span>
                            ))}
                            {candidate.licenses.map((license, idx) => (
                                <span key={idx} className="bg-green-50 text-green-700 text-xs px-3 py-1 rounded-full font-medium border border-green-200 flex items-center gap-1">
                                    <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                                    </svg>
                                    {license}
                                </span>
                            ))}
                        </div>
                    </div>

                    <div className="text-right">
                        <button className="bg-white border border-gray-300 text-gray-700 px-4 py-2 rounded-lg text-sm font-medium hover:bg-gray-50 transition-colors shadow-sm">
                            View Full CV
                        </button>
                    </div>
                </div>
            </div>

            {/* Qualifying Matches */}
            <div className="bg-gray-50 p-6">
                <div className="flex justify-between items-center mb-4">
                    <h4 className="font-bold text-gray-900 flex items-center gap-2">
                        <span className="w-2 h-6 bg-evernurse-teal rounded-full"></span>
                        Qualifying Job Matches ({candidate.totalQualifyingJobs})
                    </h4>
                    {candidate.qualifyingMatches.length > 2 && (
                        <button
                            onClick={() => setExpanded(!expanded)}
                            className="text-evernurse-teal text-sm font-medium hover:text-[#008f85] hover:underline"
                        >
                            {expanded ? 'Show Less' : 'Show All'}
                        </button>
                    )}
                </div>

                <div className="space-y-3">
                    {(expanded ? candidate.qualifyingMatches : candidate.qualifyingMatches.slice(0, 2)).map((match, idx) => (
                        <div key={idx} className="bg-white border border-gray-200 rounded-lg p-5 hover:border-evernurse-teal hover:shadow-sm transition-all">
                            <div className="flex justify-between items-start mb-3">
                                <div>
                                    <h5 className="font-bold text-gray-900 text-lg">{match.jobTitle}</h5>
                                    <p className="text-sm text-gray-600">{match.department} â€¢ {match.location}</p>
                                </div>
                                <div className="text-right">
                                    <div className="flex items-center gap-3">
                                        <div className="text-right">
                                            <p className="text-2xl font-bold text-evernurse-teal">{match.score}%</p>
                                            <p className="text-xs text-gray-500">Threshold: {match.threshold}%</p>
                                        </div>
                                        <div className="w-10 h-10 rounded-full bg-teal-50 flex items-center justify-center text-evernurse-teal">
                                            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                                            </svg>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            {/* Progress Bar */}
                            <div className="w-full bg-gray-100 rounded-full h-2 mb-4">
                                <div
                                    className="bg-evernurse-teal h-2 rounded-full transition-all duration-500"
                                    style={{ width: `${match.score}%` }}
                                ></div>
                            </div>

                            {/* Match Details */}
                            <div className="text-sm bg-gray-50 p-3 rounded-lg border border-gray-100">
                                <p className="text-gray-700 mb-2">
                                    <strong className="text-evernurse-dark">Why it matches:</strong> {match.reasons.join(' â€¢ ')}
                                </p>
                                {match.matchedSkills.length > 0 && (
                                    <div className="flex flex-wrap gap-1">
                                        {match.matchedSkills.map((skill, skillIdx) => (
                                            <span key={skillIdx} className="bg-white text-gray-600 text-xs px-2 py-1 rounded border border-gray-200">
                                                {skill}
                                            </span>
                                        ))}
                                    </div>
                                )}
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* Actions */}
            <div className="border-t border-gray-200 px-6 py-4 bg-white flex gap-3">
                {candidate.email && (
                    <a
                        href={`mailto:${candidate.email}`}
                        className="flex-1 bg-evernurse-teal text-white px-4 py-2.5 rounded-lg text-sm font-medium hover:bg-[#008f85] transition-colors text-center shadow-sm"
                    >
                        ðŸ“§ Contact Candidate
                    </a>
                )}
                <button className="flex-1 bg-white border border-gray-300 text-gray-700 px-4 py-2.5 rounded-lg text-sm font-medium hover:bg-gray-50 transition-colors">
                    ðŸ’¾ Save Profile
                </button>
            </div>
        </div>
    );
}
